.Button {
  all: initial;
  user-select: none;
  background-color: #BF2604;
  border-radius: 10px;
  padding: 20px;
  display: inline-block;
  font-size: calc(3px + 2vmin);
  text-transform: uppercase;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  letter-spacing: 2px;
  box-shadow: -4px 4px 0px 0px #A21E00;
  color: #FFF;
  cursor: pointer;
  text-decoration: none;
  margin: 4px 4px 0px 0px;
  transition: margin 0.1s, box-shadow 0.1s;
}

.Button:hover {
  background-color: #D02B06;
}

.Button:visited {
  text-decoration: none;
}

.Button:active {
  box-shadow: none;
  margin-left: -4px;
  margin-top: 4px;
}

@media only screen and (max-width: 768px) {
  .Button {
    font-size: 11pt;
  }
}