.App {
  font-family: 'Varela Round', sans-serif;
  text-align: center;
  background: rgb(240, 158, 52);
  background: url(/static/media/bg-pattern.0dc0248a.png), radial-gradient(circle, rgba(240, 158, 52, 1) 0%, rgba(242, 135, 5, 1) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-layout {
  width: 80vw;
  font-size: calc(10px + 2vmin);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.App-logo-left {
  width: 30vw;
  text-align: left;
}

.App-logo-right {
  margin-left: 5vw;
  text-align: left;
  width: 45vw;
}

.App-logo {
  margin-bottom: 20px;
}

@media only screen and (max-width: 768px) {
  .App-layout {
    flex-direction: column-reverse;
    align-items: unset;
  }
  .App-stork {
    -webkit-transform: scale(-1, 1);
            transform: scale(-1, 1);
  }
  .App-logo-left {
    width: auto;
  }
}
.LoadingPane-contents {
  color: #fff;
  font-size: calc(5px + 2vmin);
  text-shadow: 2px 2px 0 #8A8A8A;
}
.Spinner-heart {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transform-origin: 40px 40px;
          transform-origin: 40px 40px;
}

.Spinner-heart div {
  top: 32px;
  left: 32px;
  position: absolute;
  width: 32px;
  height: 32px;
  background: #fff;
  -webkit-animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
          animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Spinner-heart div:after, .Spinner-heart div:before {
  content: " ";
  position: absolute;
  display: block;
  width: 32px;
  height: 32px;
  background: #fff;
}

.Spinner-heart div:before {
  left: -24px;
  border-radius: 50% 0 0 50%;
}

.Spinner-heart div:after {
  top: -24px;
  border-radius: 50% 50% 0 0;
}

@-webkit-keyframes lds-heart {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  5% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  39% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
  }
  45% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

@keyframes lds-heart {
  0% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  5% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  39% {
    -webkit-transform: scale(0.85);
            transform: scale(0.85);
  }
  45% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  }
  100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}
