@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

.App {
  font-family: 'Varela Round', sans-serif;
  text-align: center;
  background: rgb(240, 158, 52);
  background: url('img/bg-pattern.png'), radial-gradient(circle, rgba(240, 158, 52, 1) 0%, rgba(242, 135, 5, 1) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-layout {
  width: 80vw;
  font-size: calc(10px + 2vmin);
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.App-logo-left {
  width: 30vw;
  text-align: left;
}

.App-logo-right {
  margin-left: 5vw;
  text-align: left;
  width: 45vw;
}

.App-logo {
  margin-bottom: 20px;
}

@media only screen and (max-width: 768px) {
  .App-layout {
    flex-direction: column-reverse;
    align-items: unset;
  }
  .App-stork {
    transform: scale(-1, 1);
  }
  .App-logo-left {
    width: auto;
  }
}